import React, {useState,useEffect,useRef,useCallback} from 'react';
import './Planning.scss';
import Calendar from "@toast-ui/react-calendar";
import 'tui-calendar/dist/tui-calendar.css';
import 'tui-date-picker/dist/tui-date-picker.css';
import 'tui-time-picker/dist/tui-time-picker.css';
import NoteAltIcon from '@mui/icons-material/NoteAlt';
import NotesAside from '../../components/Notes/NotesAside';
import TaskEstimatesPlan from '../../components/TaskEstimatesPlan/TaskEstimatesPlan';
import TextField from '@mui/material/TextField';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import Button from '@mui/material/Button';
import { useDispatch } from 'react-redux';
import {
  actionSetNotesFor
} from '../../app/Reducers/note/noteSlice';
import {
  actionGetTeamMembers
} from '../../app/Reducers/user/userSlice';
import {
  actionRecordPlan
} from '../../app/Reducers/planning/planningSlice';


const start = new Date();
const end = new Date(new Date().setMinutes(start.getMinutes() + 30));
const schedules = [
  {
    calendarId: "1",
    category: "time",
    isVisible: true,
    title: "Study",
    id: "1",
    body: "Test",
    start,
    end
  },
  {
    calendarId: "2",
    category: "time",
    isVisible: true,
    title: "Meeting",
    id: "2",
    body: "Description",
    start: new Date(new Date().setHours(start.getHours() + 1)),
    end: new Date(new Date().setHours(start.getHours() + 2))
  }
];

const calendars = [
  {
    id: "1",
    name: "My Calendar",
    color: "#ffffff",
    bgColor: "#9e5fff",
    dragBgColor: "#9e5fff",
    borderColor: "#9e5fff"
  },
  {
    id: "2",
    name: "Company",
    color: "#ffffff",
    bgColor: "#00a9ff",
    dragBgColor: "#00a9ff",
    borderColor: "#00a9ff"
  }
];

const Planning = ({

}) => {
    const cal = useRef(null);
    const [showNotes, setShowNotes] = useState(false)
    const [calendarSchedule, setCalendarSchedule] = useState(schedules)
    const dispatch = useDispatch();
    const [noteTextList, setNoteTextList] = useState([]);
    const [calendarValue, setCalendarValue] = useState(new Date())
    const onSelectedDateChange = useCallback((newCalendarValue) => {
      const calendar = cal.current.calendarInst;
      calendar.setDate(newCalendarValue)
      setCalendarValue(newCalendarValue);
    },[calendarValue])
    
    const onClickSchedule = useCallback((e) => {
      const { calendarId, id } = e.schedule;
      const el = cal.current.calendarInst.getElement(id, calendarId);
  
      console.log(e, el.getBoundingClientRect());
    }, []);
  
    const onBeforeCreateSchedule = useCallback((scheduleData) => {
      const schedule = {
        id: String(Math.random()),
        title: scheduleData.title,
        isAllDay: scheduleData.isAllDay,
        start: scheduleData.start,
        end: scheduleData.end,
        category: scheduleData.isAllDay ? "allday" : "time",
        dueDateClass: "",
        location: scheduleData.location,
        // raw: {
        //   class: scheduleData.raw["class"]
        // },
        state: scheduleData.state
      };

      console.log(schedule)
  
      cal.current.calendarInst.createSchedules([schedule]);
    }, []);
  
    const onBeforeDeleteSchedule = useCallback((res) => {
      console.log(res);
  
      const { id, calendarId } = res.schedule;
  
      cal.current.calendarInst.deleteSchedule(id, calendarId);
    }, []);
  
    // on calendar view update
    const onBeforeUpdateSchedule = useCallback((e) => {
      
      const { schedule, changes } = e;

      cal.current.calendarInst.updateSchedule(
        schedule.id,
        schedule.calendarId,
        changes
      );

      const calObjectItems = cal.current.calendarInst._controller.schedules.items;
      let updatedTime = 0;
      
      for (const [key, value] of Object.entries(calObjectItems)) {
        if(value.body == schedule.body){
          updatedTime += (value.end.getTime() - value.start.getTime()) / 1000 / 60 / 60;
        }
      }

      setNoteTextList(noteTextList => [...noteTextList].map(noteData => {
        return noteData.id == schedule.body ? {
          ...noteData,
          estimate: updatedTime
        } : noteData
      }))
      
    }, [noteTextList]);

    const _getFormattedTime = (time) => {
      const date = new Date(time);
      const h = date.getHours();
      const m = date.getMinutes();
  
      return `${h}:${m}`;
    }
  
    const _getTimeTemplate = (schedule, isAllDay) => {
      var html = [];
  
      if (!isAllDay) {
        html.push("<strong>" + _getFormattedTime(schedule.start) + "</strong> ");
      }
      if (schedule.isPrivate) {
        html.push('<span class="calendar-font-icon ic-lock-b"></span>');
        html.push(" Private");
      } else {
        if (schedule.isReadOnly) {
          html.push('<span class="calendar-font-icon ic-readonly-b"></span>');
        } else if (schedule.recurrenceRule) {
          html.push('<span class="calendar-font-icon ic-repeat-b"></span>');
        } else if (schedule.attendees.length) {
          html.push('<span class="calendar-font-icon ic-user-b"></span>');
        } else if (schedule.location) {
          html.push('<span class="calendar-font-icon ic-location-b"></span>');
        }
        html.push(" " + schedule.title);
      }
  
      return html.join("");
    }
  
    const templates = {
      time: function (schedule) {
        return _getTimeTemplate(schedule, false);
      }
    };

    const savePlan = (selectedUser,selectedDateRange) => {
      
      if(selectedUser){
        // See the calendar instance
        const calendarPlanData = Object.entries(cal.current.calendarInst._controller.schedules.items).map(([key,value]) => value);
        // pass the following to the API
        dispatch(actionRecordPlan(
          selectedUser,
          noteTextList,
          JSON.stringify(calendarPlanData),
          selectedDateRange
        ))
      }
      else {
        alert("Select a member to plan")
      }
      
    }

    const next = useCallback(() => {
      const calendar = cal.current.calendarInst;
      calendar.next()
    },[])
  
    const prev = useCallback(() => {
      const calendar = cal.current.calendarInst;
      calendar.prev()
    },[])

    const today = useCallback(() => {
      const calendar = cal.current.calendarInst;
      calendar.today()
    },[])

    const removeTaskInNoteList = selectedIndex => {
      console.log("dumaaanhere")
      console.log(noteTextList)
      setNoteTextList(noteTextList.filter((taskIndex,i) => i != selectedIndex))
    }

    useEffect(()=>{
      dispatch(actionGetTeamMembers())
      dispatch(actionSetNotesFor("Planning"))
    },[])
  
    return (
      <div className="PlanningContainer">
        <div className="header-container">
          <h1>Planning</h1>
          <div className="actions-wrapper">
            <Button 
            onClick={()=>setShowNotes(show=>!show)}
            aria-label="note"
            variant="contained" startIcon={<NoteAltIcon fontSize="inherit" />}>
              Planning Notes
            </Button>
            {/* <Button 
              onClick={()=>{
                console.log(
                  cal
                  .current
                  .props
                  .schedules
                )
                setCalendarSchedule(cal
                  .current
                  .props
                  .schedules)
              }}
              aria-label="note"
              variant="outlined"
              startIcon={<NoteAltIcon fontSize="inherit" />}>
              test calendar
            </Button>
            <Button 
            onClick={()=>{
              // console.log(cal.current.calendarInst.getSchedule())
              const x = cal.current
              console.log(x)
              // setCalendarSchedule()
            }}
            aria-label="note"
            variant="outlined" startIcon={<NoteAltIcon fontSize="inherit" />}>
              clear
            </Button> */}
          </div>
        </div>
        <div className="planning-wrapper">
          <div className="calendar-layout">
            <div className="calendar-actions-wrapper">
              <LocalizationProvider dateAdapter={AdapterDateFns}>
                <DatePicker
                    label="Select Date"
                    value={calendarValue}
                    onChange={onSelectedDateChange}
                    renderInput={
                      (params) => <TextField
                                  size="small"
                                  variant="standard"
                                  {...params}/>
                    }
                />
              </LocalizationProvider>
              <div className="move-calendar-date-actions-wrapper">
                <Button onClick={()=>prev()} variant="outlined">Prev</Button>
                <Button onClick={()=>today()} variant="outlined">Today</Button>
                <Button onClick={()=>next()} variant="outlined">Next</Button>
              </div>
            </div>
            <Calendar
              ref={cal}
              height="1000px"
              view="week"
              useCreationPopup={true}
              useDetailPopup={true}
              template={templates}
              calendars={calendars}
              schedules={calendarSchedule}
              taskView={false}  // e.g. true, false, or ['task', 'milestone']
              scheduleView={['time']} // e.g. true, false, or ['allday', 'time']
              onClickSchedule={onClickSchedule}
              onBeforeCreateSchedule={onBeforeCreateSchedule}
              onBeforeDeleteSchedule={onBeforeDeleteSchedule}
              onBeforeUpdateSchedule={onBeforeUpdateSchedule}
            />
          </div>
          <TaskEstimatesPlan
            setCalendarSchedule = {setCalendarSchedule}
            noteTextList = {noteTextList}
            setNoteTextList = {setNoteTextList}
            savePlan = {savePlan}
            removeTaskInNoteList = {removeTaskInNoteList}
          />
        </div>

        <NotesAside
        showNotes={showNotes}
        setShowNotes={setShowNotes}
        fixed={true}
        enableCopyText={true}/>
      </div>
    );
}

export default Planning;