//import EditNote from '.components/EditNote/EditNote';//cut it and paste to the component
import React, { useEffect } from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import * as yup from "yup";
import { useForm, Controller } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import TextField from '@mui/material/TextField';
import CloseIcon from '@mui/icons-material/Close';
import { useDispatch } from 'react-redux';
import {
    actionUpdateNotes
} from '../../app/Reducers/note/noteSlice';

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
};

const schema = yup.object().shape({
    noteTitle: yup.string().required(),
});


const EditNote = ({
    note,
    editNoteConfirmationOpen,
    setEditNoteConfirmationOpen,
    updateNote
}) => {

    const dispatch = useDispatch();

    // form setup
    const { control, handleSubmit, reset, formState: {errors,isSubmitSuccessful} } = useForm({ 
        defaultValues: {
            noteTitle: note.title
        },
        resolver: yupResolver(schema)
    });

    // reset the form after successfull submit of form data
    useEffect(() => {
        if (isSubmitSuccessful) {
            // setEditNoteConfirmationOpen(false)
        };
    }, [isSubmitSuccessful, reset]);

    // on form submit
    const onSubmit = ({noteTitle}) => {
        // updateNote({
        //     ...note,
        //     title: noteTitle
        // })
        dispatch(actionUpdateNotes({
            ...note,
            title: noteTitle
        }))

        setEditNoteConfirmationOpen(false)
    }

    return <div className='EditNoteContainer'>
        <Modal
            keepMounted
            open={editNoteConfirmationOpen}
            onClose={()=>setEditNoteConfirmationOpen(false)}
            aria-labelledby="keep-mounted-modal-title"
            aria-describedby="keep-mounted-modal-description"
        >
            <Box sx={style} spacing={2}>
                <Stack
                direction="row"
                justifyContent="space-between"
                alignItems="flex-start"
                spacing={2}
                style={{marginBottom:25}}>

                    <Typography id="keep-mounted-modal-title" variant="h6" component="h2">
                        {`Update Note Title`}
                    </Typography>

                    <CloseIcon 
                    className="close-form-trigger"
                    onClick={()=>setEditNoteConfirmationOpen(false)}/>

                </Stack>
                <form className="todo-edit-form" onSubmit={handleSubmit(onSubmit)}>
                    <Stack spacing={2}>

                        <Controller
                        name="noteTitle"
                        control={control}
                        defaultValue={""}
                        render={({field}) => <TextField
                            {...field}
                            className="textArea"
                            label="Note Name"
                            variant="outlined"
                            inputRef={input => input && setTimeout(() =>{
                                input.focus()
                            },100)}
                            placeholder={"Note Name"}
                            error={!!errors.noteTitle}
                            type="text"
                            multiline
                            helperText={errors?.noteTitle ? errors?.noteTitle?.message : ''}
                        />
                        }/>

                        <Stack
                        spacing={1}
                        direction="row"
                        justifyContent="flex-end">
                            <Button type="submit" variant="outlined">Save</Button>
                            <Button onClick={()=>setEditNoteConfirmationOpen(false)} type="button" variant="outlined">Cancel</Button>
                        </Stack>

                    </Stack>
                </form>
            </Box>
        </Modal>
    </div>
}

export default EditNote;