import React from 'react';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import "./WeeklyAverageData.scss";
import { useSelector } from 'react-redux';

const WeeklyAverageData = ({

}) => {
    const { 
        averageCountData
    } = useSelector(state => state.dashboard);
    
    console.log("test")

    return <div className="weekly-average-data-container member-data-container">
        <div className="card-list-container">
            {averageCountData.map(e=>{
                const comparison = e.previous;
                const comparisonValue = e.current - e.previous;
                const isNegative = comparison ? comparisonValue <= 0 : 0;
                const weeklyComparisonVal = comparison ? comparisonValue * (isNegative ? -1 : 1) : 0;

                return <div className="graphContainer" key={e.id}>
                    <div className="data-card">
                        <p className="card-title"><span className="icon"><AccessTimeIcon/></span><span className="text">{e.name}</span></p>
                        <p className="value">{e.id!="bugsFixed" ? e.current.toFixed(2) : e.current.toFixed(0)}</p>
                        {e.id!="bugsFixed" ? <p className="percent-comparison-container">
                            <span className={`percent-comparison ${isNegative ? "red" : "green"}`}>{(isNegative ? "-" : "+") + weeklyComparisonVal.toFixed(2)}</span> From Last week
                        </p> : <p className="percent-comparison-container"></p>}
                    </div>
                </div>}
            )}
        </div>
    </div>
}

export default WeeklyAverageData;
