export const actionRegister = thirdPartyUserData => dispatch => {
    console.log(thirdPartyUserData)
    Axios.post(
        `${URI}/users/registration`,
        thirdPartyUserData
    ).then( res => {
        console.log(res.data)
        dispatch(setUserData(res.data))

        // dispatch(updateTodoInList(res.data))
    }).catch(error => {
        console.log(error);
        dispatch(setShowError("Email already exist"))
    });
}

export const actionLogin = thirdPartyUserData => async dispatch => {
    try{
        const user = await Axios.post(
            `${URI}/users/login`,
            thirdPartyUserData
        )
        localStorage.setItem("wstoken", user.data.token)
        dispatch(setUserData(user.data.userData))
        return true
    } catch(err){ return false }
    
    // .then( res => {
    //     console.log(res.data)
    
    // })
}

export const actionLogout = () => dispatch => {
    console.log("dumaan dito")
    localStorage.clear()
    window.location.href = "/";
}

export const actionAuthenticateToken = () => async dispatch => {

    const headers = getHeaders();
    if(headers == null) { 
        alert("Please login your account")
        return 0
    }

    Axios.get(
        `${URI}/users/authenticate-token`,
        headers
    ).then( res => {
        console.log(res)
        dispatch(setUserData(res.data))
    })
}

export const actionShowAccountRegistration = show => dispatch => {
    dispatch(setShowRegistration(show));
}

export const actionGetTeamMembers = () => async dispatch => {

    const headers = getHeaders();
    if(headers == null) return

    console.log("nandito sa actionGetTeamMembers")
    const res = await Axios.get(`${URI}/users/team-members`,headers)
    console.log(res.data)
    dispatch(setTeamMembers(res.data))
}

export const thirdPartyUserData = state => state.user.thirdPartyUserData;
export const calendarSchedules = state => state.user.thirdPartyUserData;

export default userSlice.reducer;