/*
This function requred this kind of data structure:
[ {_id, ...}, {_id, ...}, ... ]
*/
const uniqueArrayOfObject = (a) => { 
    var seen = {};
    return a.filter(({_id}) => seen.hasOwnProperty(_id) ? false : (seen[_id] = true) );
}

