export const dashboardSlice = createSlice({
    name: 'dashboard',
    initialState,
    reducers: {
        setWeeklyBurnoutIndex: (state, action) => {
            state.weeklyBurnoutIndex = action.payload
        },
        setWeeklyProductivityAndEvent: (state, action) => {
            state.weeklyProductivityAndEvent = action.payload
        },
        setAverageCountData: (state, action) => {
            state.averageCountData = action.payload
        },
        setBurnoutIndexData: (state, action) => {
            state.burnoutIndexData = action.payload
        },
        setMomentPerHourDefaultValues: (state, action) => {
            state.momentPerHourDefaultValues = action.payload
        },
        setWeeklyProductivity: (state, action) => {

            const {averageCountData,calendarRecords} = action.payload;

            state.averageCountData = averageCountData;

            state.weeklyBurnoutIndex = {
                id: "weeklyBurnoutIndex",
                name: "Weekly Burnout Index",
                data: calendarRecords.map(calRec=>{
                    const breakAndProductiveHours = calRec.breakHours + calRec.productiveHours;
                    const yVal = breakAndProductiveHours ? calRec.productiveHours / breakAndProductiveHours : 0;
                    return {
                        x: getDayName(calRec.date), y: yVal
                    }
                })
            }

            state.weeklyProductivityAndEvent = [
                {
                    id: "productiveHours",
                    name: "Productive hours",
                    data: calendarRecords.map(calRec=>{
                        return {
                            x: getDayName(calRec.date), y:calRec.productiveHours
                        }
                    })
                },
                {
                    id: "numberOfEvents",
                    name: "Number of events",
                    data: calendarRecords.map(calRec=>{
                        return {
                            x: getDayName(calRec.date), y:calRec.tasks.length
                        }
                    })
                }
            ]

            const tempTodoTypes = { "0": 0, "1": 0, "2": 0, "3": 0 }
            const tempTodoStatus = { "0": 0, "1": 0, "2": 0, "3": 0 }
            const allTaskWithinDateRange = uniqueArrayOfObject(calendarRecords.reduce( (a, calRec)=>[...a,...calRec.tasks],[]))
            
            state.allTaskWithinDateRange = allTaskWithinDateRange;

            allTaskWithinDateRange.forEach(element => {
                const typeIndex = element.hasOwnProperty("type") ? element.type : 0; // 0 means working
                const statusIndex = element.hasOwnProperty("status") ? element.status : 3; // 3 means done
                tempTodoTypes[typeIndex] = tempTodoTypes[typeIndex] + 1
                tempTodoStatus[statusIndex] = tempTodoStatus[statusIndex] + 1
            });

            /* This function will return the data in this format: 
            [{ label: "Working", count: 0 },
             { label: "Learning", count: 0 },
             { label: "Learning and working", count: 0 },
             { label: "Administration", count: 0 }] */

            state.todoTypes = Object.entries(tempTodoTypes).reduce((a, [key, value]) => {
                return a.map(data => {
                    if(data.typeCode == parseInt(key)){
                        return { ...data, count: value }
                    }
                    return data
                })
            },[...state.todoTypes])

            /* This function will return the data in this format: 
            [{ label: "Not Started", count: 0 },
             { label: "On Going", count: 0 },
             { label: "Paused", count: 0 },
             { label: "Done", count: 0 }] */
            state.todoStatus = Object.entries(tempTodoStatus).reduce((a, [key, value]) => {
                return a.map(data => {
                    if(data.statusCode == parseInt(key)){
                        return { ...data, count: value }
                    }
                    return data
                })
            }, [...state.todoStatus])

            const lastElement = [...calendarRecords].pop();
            // console.log(lastElement)
            const totalLoggedHours = lastElement.productiveHours + lastElement.breakHours
            const burnoutIndex = lastElement.productiveHours / totalLoggedHours;
            state.burnoutIndexData = [
                {id: "burnoutIndex", name: "Burnout Index", value: burnoutIndex},
                {id: "productiveHours", name: "Productive Hours", value: lastElement.productiveHours},
                {id: "breakHours", name: "Break Hours", value: lastElement.breakHours},
                {id: "totalLoggedHours", name: "Total Logged Hours", value: totalLoggedHours}
            ]
            state.dailyProductivityData = [
                {
                    count: lastElement.productiveHours,
                    label: "Productive Hours"
                },
                {
                    count: (24-lastElement.productiveHours),
                    label: "Unlogged Hours"
                }
            ]
        }
    }
});

