//import SubNavigation from '.components/SubNavigation/SubNavigation';//cut it and paste to the component
import React, {useState,useRef} from 'react';
import IconButton from '@mui/material/IconButton';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';

const SubNavigation = ({
    link,
    children
}) => {

    const [open, setopen] = useState(false)
    const childrenElementsRef = useRef(null);
    const [childrenHeight, setChildrenHeight] = useState(0)

    return <div className='SubNavigationContainer'>

        <div className="parent-navigation-wrapper">
            {link}
            <span className={`trigger ${open ? "open" : ""}`}>
                <IconButton
                color="primary"
                aria-label="delete"
                size="small"
                onClick={()=>{
                    if(open){
                        setopen(false)
                        setChildrenHeight(0);
                    }
                    else{
                        setopen(true)
                        setChildrenHeight(childrenElementsRef.current.offsetHeight); 
                    }
                }}>
                    <KeyboardArrowUpIcon fontSize="inherit" />
                </IconButton>
            </span>
        </div>

        <div
        className="hidden-content"
        style={{
            height: childrenHeight,
            transition: "all 300ms ease",
            overflow: "hidden",
            borderRadius: "8px"
        }}
        >
            <div ref={childrenElementsRef}>
                {children}
            </div>
        </div>

    </div>

}

export default SubNavigation;