const initialState = {
    chatList: [{
        _id: "00000000000", // this is the chat id
        name: "Big Bro", 
        conversation: [],
        seen: false
    }],
    showOptions: false,
    subjects: [],
    links: []
};

export const chatSlice = createSlice({
    name: 'chats',
    initialState,
    reducers: {
        sendMessage: (state,action) => {
            console.log("Dumaan sa sendMessage function...")
            console.log(action.payload)
            const { chat_id } = action.payload[0];

            const messageData = action.payload.map(chat => {
                return {
                    ...chat,
                    _id: uuid()
                }
            })
            
            state.chatList = state.chatList.map(chatData=>{
                console.log(messageData);

                // not entering in if...
                if(chatData._id === chat_id){ // find the chat data in the chat list
                    return {
                        ...chatData,
                        conversation: [
                            ...chatData.conversation,
                            ...messageData
                        ]
                    }
                }
                return chatData
            })

            console.log(state.chatList)
        },
        sendMessageToBot: (state, action) => {
            
            console.log(action.payload)
            const { chat_id, messages, subjects, links, showOptions } = action.payload;

            const messageData = messages.map(chat => {
                return {
                    ...chat,
                    _id: uuid()
                }
            })
            
            state.chatList = state.chatList.map(chatData=>{
                console.log(messageData);

                // not entering in if...
                console.log(`${chatData._id} === ${chat_id}`)
                if(chatData._id === chat_id){ // find the chat data in the chat list
                    return {
                        ...chatData,
                        conversation: [
                            ...chatData.conversation,
                            ...messageData
                        ]
                    }
                }
                return chatData
            })

            state.subjects = subjects ? subjects : []
            state.links = links ? links: []
            state.showOptions = showOptions

            console.log(state.chatList)
        },
        removeOptionsInChatbox: (state, action) => {
            state.showOptions = false
        }
    }
});

export const {
    sendMessage,
    sendMessageToBot,
    removeOptionsInChatbox
} = chatSlice.actions;

export const actionSendMessage = messageData => (dispatch,getState) => {
    dispatch(sendMessage(messageData))
}

export const actionRemoveOptionsInChatbox = () => dispatch => {
    dispatch(removeOptionsInChatbox())
}

