//import TodoSideNav from '.components/TodoSideNav/TodoSideNav';//cut it and paste to the component
import React, {useState,useCallback} from 'react';
import './TodoSideNav.scss';
import Stack from '@mui/material/Stack';
import CloseIcon from '@mui/icons-material/Close';
import moment from 'moment';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import UpdateScheduleTime from './UpdateScheduleTime';
import { getTodoStatus } from '../Hooks/Constants';
import MomentForm from '../Moments/MomentForm'
import MomentList from '../Moments/MomentList'
import { useSelector, useDispatch } from 'react-redux';
import {
    actionOpenSideNav
} from '../../app/Reducers/todo/todoSlice';
import TodoActions from './TodoActions'
import { ReactComponent as UserIcon } from '../../Assets/ic_user.svg';
import TodoTags from './TodoTags/TodoTags';
import TodoMoreOptions from './TodoMoreOptions';

const TodoSideNav = ({
    updateSchedule,
    fixed=false
}) => {

    const [momentToEdit, setMomentToEdit] = useState(null);
    const [partialDesc, setPartialDesc] = useState(true);
    const dispatch = useDispatch();

    const {
        todoInSideNav,
        scheduleInSideNav,
        isSideNavOpen
    } = useSelector(state => state.todo);

    const [updateScheduleModalOpen, setUpdateScheduleModalOpen] = useState(false);

    const getTimeStarted = useCallback(() => {
        if(scheduleInSideNav){
            return  moment(scheduleInSideNav.start).format('LLL')
        }
        else if(todoInSideNav){
            console.log(todoInSideNav)
            return moment(todoInSideNav.start).format('LLL')
        }
        return "Not Started"
    })

    const getTimeEnded = useCallback(() => {
        if(scheduleInSideNav){
            if(scheduleInSideNav.end)
                return moment(scheduleInSideNav.end).format('LLL')
        }
        else if(todoInSideNav){
            if(todoInSideNav.end)
                return moment(todoInSideNav.end).format('LLL')
        }
        return "Not Ended"
    })

    const getScheduleData = () => {
        const { assignedTo: { firstName, lastName } } = sideNavTodoData;

        return <div className="todo-info-section">
            <p className="todo-detail asignee-info">
                <span className="icon"><UserIcon/></span>
                <span className="label">Assignee: </span>
                <span className="value">{ firstName } {lastName }</span>
            </p>
            <p
            className="todo-detail clickable"
            onClick={()=>setUpdateScheduleModalOpen(true)}>
                <span className="icon"><AccessTimeIcon sx={{
                    width: "20px",
                    heigth: "20px"
                }}/></span>
                <span className="label">Start Time: </span>
                <span className="value">{getTimeStarted()}</span>
            </p>
            <p
            className="todo-detail clickable"
            onClick={()=>setUpdateScheduleModalOpen(true)}>
                <span className="icon"><AccessTimeIcon sx={{
                    width: "20px",
                    heigth: "20px"
                }}/></span>
                <span className="label">End Time: </span>
                <span className="value">{getTimeEnded()}</span>
            </p>
            <UpdateScheduleTime
                schedule={scheduleInSideNav}
                updateScheduleModalOpen={updateScheduleModalOpen}
                setUpdateScheduleModalOpen={setUpdateScheduleModalOpen}
                updateSchedule={updateSchedule}
            />
        </div>
    }

    const sideNavTodoData = todoInSideNav ? todoInSideNav : scheduleInSideNav;
    const statusData = getTodoStatus(sideNavTodoData?.status);

    return <div className={`TodoSideNavContainer ${fixed ? "fixed" : ""} ${isSideNavOpen ? "open":""}`}>
       <div className="side-nav-wrapper">
            <Stack
            direction="row"
            justifyContent="space-between"
            alignItems="center"
            spacing={2}
            style={{
                padding: "0 2px 19px",
                borderBottom: "1px solid #E7E7E7"
            }}>
                <h2 className="section-title-01">Todo Details</h2>
                {sideNavTodoData ? <TodoActions todo={sideNavTodoData}/> : ""}
                <CloseIcon 
                className="close-form-trigger"
                onClick={()=>dispatch(actionOpenSideNav(false))}/>
            </Stack>
            {sideNavTodoData ? <div className="todo-info-wrapper">
                <TodoTags todo={sideNavTodoData}/>
                <TodoMoreOptions todo={sideNavTodoData}/>
                <p className="todo-name">{sideNavTodoData?.name}</p>
                {sideNavTodoData?.description?.length ? <>
                    <p className={`todo-description ${partialDesc ? "one-line" : ""}`}>{sideNavTodoData.description}</p>
                <p
                className="todo-more-trigger"
                onClick={()=>setPartialDesc(!partialDesc)}
                >{partialDesc ? "More..." : "Less..."}</p>
                </> : ""}
                {/* Means that you selected a schedule in the calendar. Not todo data from network request */}
                {getScheduleData()}
            </div> : ""}
            <MomentList
            momentList={sideNavTodoData ? sideNavTodoData.moments : []}
            setMomentToEdit={setMomentToEdit}
            />
        </div>

        <div className="moment-form-wrapper">
            {scheduleInSideNav || sideNavTodoData ? <MomentForm
                        todoId={sideNavTodoData?._id}
                        momentToEdit={momentToEdit}
                        setMomentToEdit={setMomentToEdit}
                        // setMomentList={setMomentList}
                        /> : ""}
        </div>
    </div>
}

export default TodoSideNav;