//import TodoMoreOptions from '.components/TodoMoreOptions/TodoMoreOptions';//cut it and paste to the component
import React, { useState } from 'react';
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemIcon from '@mui/material/ListItemIcon';
import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import {v1 as uuid} from "uuid";
import EditTodo from "./EditTodo";
import DeleteTodo from "./DeleteTodo"
import { useDispatch } from 'react-redux';
import {
    actionDeleteTodo,
    actionSetTodoPopupMessage
} from '../../app/Reducers/todo/todoSlice';
// import PopupNotifications from '../PopupNotifications/PopupNotifications';



const TodoMoreOptions = ({
    todo
}) => {
    const dispatch = useDispatch();
    // const {
    //     popupType,
    //     popupMessage
    // } = useSelector(state => state.todo)

    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);
    const [editTodoOpen, setEditTodoOpen] = useState(false);
    const [deleteConfirmationOpen, setDeleteConfirmationOpen] = useState(false);
    // const [showPopupNotification, setshowPopupNotification] = useState(false)
    // const [popupMsg, setPopupMsg] = useState("")
    // const [severity, setSeverity] = useState("success")
    
    // const handlePopupClose = () => {
    //     setshowPopupNotification(false);
    //     // setshowPopupNotification(true)
    // }

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => setAnchorEl(null);

    const handleDelete = () => {
        console.log("dumaan dito")
        console.log(todo)
        console.log(todo?.moments?.length)
        console.log(todo.status)
        if(todo?.moments?.length == 0 && todo.status == 0){
            setDeleteConfirmationOpen(true)
        } else{
            dispatch(actionSetTodoPopupMessage(true,`error`,`"You cannot delete Todo with moments, already started, paused or done. Also only the user assigned in Todo can delete it."`))
        }
    }

    const onDeleteTodo = todoId => {
        console.log("Dumaan sa onDeleteTodo " + todoId)
        dispatch(actionDeleteTodo(todoId))
    }

    const menuOptions = [
        {
            id: uuid(),
            name: "Edit",
            icon: <EditIcon fontSize="small"/>,
            callback: () => setEditTodoOpen(true)
        },
        {
            id: uuid(),
            name: "Delete",
            icon: <DeleteIcon fontSize="small"/>,
            callback: () => handleDelete()
        }
    ];

    const getMenuItems = () => {
        return menuOptions.map(option=>(
            <MenuItem
            key={option.id}
            onClick={()=>{
                option.callback()
                setAnchorEl(null)
            }}>
                <ListItemIcon>
                    {option.icon}
                </ListItemIcon>
                <ListItemText>{option.name}</ListItemText>
            </MenuItem>
        ))
    }
    
    return <div className='TodoMoreOptionsContainer'>
        {/* <EditTodo/> */}
        <IconButton
        id="basic-button"
        aria-controls={open ? 'basic-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}>
            <MoreHorizIcon/>
        </IconButton>
        <Menu
            id="basic-menu"
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}
            MenuListProps={{
            'aria-labelledby': 'basic-button',
            }}
        >
            {getMenuItems()}
        </Menu>
        <EditTodo
            todo={todo}
            editTodoOpen={editTodoOpen}
            setEditTodoOpen={setEditTodoOpen}
        />
        <DeleteTodo
            todo={todo}
            deleteConfirmationOpen={deleteConfirmationOpen}
            setDeleteConfirmationOpen={setDeleteConfirmationOpen}
            onDeleteTodo={onDeleteTodo}
        />

        
        
    </div>
}

export default TodoMoreOptions;