import React, { useState } from 'react';
import TableChartIcon from '@mui/icons-material/TableChart';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import {
    RadialChart
} from 'react-vis';

const DoughnutChartWithTableSection = ({
    graphData,
    colorPallete,
    totalGraphData,
    withTableAndListToggle,
    unit,
    isPercentageData = true,
    title,
    chartWidth,
    chartHeight,
    filterZeroValues = true,
    showGraphLabel = false,
    legendsList = null,
}) => {
    const [dataView, setDataView] = useState("legend");
    return <div className='doughnutChartWithTableSectionContainer'>

        <div className="heading-container">
            <div className="heading-container">
                <h5>{title}</h5>

                {withTableAndListToggle && <button
                    className="view-toggle"
                    onClick={() => setDataView(dataView == "table" ? "legend" : "table")}>
                    {dataView == "table" ? <MoreVertIcon style={{ color:"#000fff" }} /> : <TableChartIcon style={{ color:"#000fff" }} />}
                </button>}
            </div>
        </div>
        <div className="graph-and-table-data-container">
            <div className="dataGraphContainer">
                <RadialChart
                    colorDomain={colorPallete}
                    colorRange={colorPallete}
                    innerRadius={40}
                    radius={chartWidth ? chartWidth / 2 : 140}
                    labelsRadiusMultiplier={0.85}
                    style={{ color: "#fff" }}
                    animation={true}
                    data={
                        graphData
                            // .sort((a, b) => b.count - a.count)
                            .filter(x => filterZeroValues ? x.count : true)
                            .map((x, i) => {
                                return {
                                    angle: Number(x.count),
                                    label: isPercentageData ? (x.count / totalGraphData * 100).toFixed(1) + "%" : x.count + (unit ? " " + unit : ""),
                                    style: { color: "#fff" },
                                    color: colorPallete[i % colorPallete.length]
                                }
                            })
                    }
                    width={chartWidth ? chartWidth : 300}
                    height={chartHeight ? chartHeight : 300}
                    showLabels={true}>
                </RadialChart>
            </div>
            {
                legendsList ? legendsList :
                dataView == "table" ? 
                <div className="tableDataContainer tableWrapperStyle">
                    <TableContainer style={{ overflowX: 'initial' }}>
                        <Table stickyHeader>
                            <TableHead>
                                <TableRow>
                                    <TableCell>Workspace Name</TableCell>
                                    <TableCell>Count</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody className="custom-data-legends">
                                {graphData
                                    // .sort((a, b) => b.count - a.count)
                                    .filter(x => filterZeroValues ? x.count : true)
                                    .map((x, i) => {
                                        return <TableRow key={x.i}>
                                            <TableCell align="left">
                                                <span className="table-row-name">
                                                    <span className="labelColor" style={{ backgroundColor: `${colorPallete[i % colorPallete.length]}` }}>.</span>
                                                    <span className="labelText">{x.label}</span>
                                                </span>
                                            </TableCell>
                                            <TableCell align="center">{x.count}</TableCell>
                                        </TableRow>
                                    })}
                                <TableRow>
                                    <TableCell align="left">Total</TableCell>
                                    <TableCell align="center">{totalGraphData}</TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>
                :
                <ul className="custom-data-legends">
                    {graphData
                        // .sort((a, b) => b.count - a.count)
                        .filter(x => filterZeroValues ? x.count : true)
                        .map((x, i) => <li className="legend-data" key={i}>
                            <span className="labelColor" style={{ backgroundColor: `${colorPallete[i % colorPallete.length]}` }}>.</span>
                            <span className="labelText">{x.label} ({x.count}{unit && " " + unit})</span>
                        </li>)}
                </ul>
            }
        </div>

    </div >
}

export default DoughnutChartWithTableSection;