import React, {useEffect} from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import * as yup from "yup";
import { useForm, Controller } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import TextField from '@mui/material/TextField';
import CloseIcon from '@mui/icons-material/Close';
import { useDispatch } from 'react-redux';
import {
    actionUpdateTodo
} from '../../app/Reducers/todo/todoSlice';
import RHFSelect from "../ReactHookFormElements/RHFSelect";
import { TodoTypes } from "../Hooks/Constants";
import MenuItem from '@mui/material/MenuItem';

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
};

const schema = yup.object().shape({
    todoName: yup.string().required(),
    todoDescription: yup.string().required(),
});

const EditTodo = ({
    todo,
    editTodoOpen,
    setEditTodoOpen,
}) => {

    const dispatch = useDispatch();
    // form setup
    const { control, handleSubmit, reset, formState: {errors,isSubmitSuccessful} } = useForm({ 
        defaultValues: {
            todoName: todo.name,
            todoDescription: todo.description,
            todoType: todo.type ? todo.type : 0
        },
        resolver: yupResolver(schema)
    });

    // reset the form after successfull submit of form data
    useEffect(() => {
        if (isSubmitSuccessful) {
            setEditTodoOpen(false)
            // reset({ todoName: "", todoDescription: "" });
        };
    }, [isSubmitSuccessful, reset]);

    // on form submit
    const onSubmit = ({todoName,todoDescription,todoType}) => {

        console.log({todoName,todoDescription,todoType})

        dispatch(actionUpdateTodo({
            _id: todo._id,
            name: todoName,
            description: todoDescription,
            type: todoType
        }))

    }

    return <div className='EditTodoContainer'>
        <Modal
            keepMounted
            open={editTodoOpen}
            onClose={()=>setEditTodoOpen()}
            aria-labelledby="keep-mounted-modal-title"
            aria-describedby="keep-mounted-modal-description"
        >
            <Box sx={style} spacing={2}>
                <Stack
                direction="row"
                justifyContent="space-between"
                alignItems="flex-start"
                spacing={2}
                style={{marginBottom:25}}>

                    <Typography id="keep-mounted-modal-title" variant="h6" component="h2">
                        {`Update Todo`}
                    </Typography>

                    <CloseIcon 
                    className="close-form-trigger"
                    onClick={()=>setEditTodoOpen(false)}/>

                </Stack>
                <form className="todo-edit-form" onSubmit={handleSubmit(onSubmit)}>
                    <Stack spacing={2}>

                        <Controller
                        name="todoName"
                        control={control}
                        defaultValue={""}
                        render={({field}) => <TextField
                            {...field}
                            className="textArea"
                            label="Todo Name"
                            variant="outlined"
                            // inputRef={input => input && setTimeout(() =>{
                            //     input.focus()
                            // },100)}
                            placeholder={"Todo Name"}
                            error={!!errors.todoName}
                            type="text"
                            multiline
                            helperText={errors?.todoName ? errors?.todoName?.message : ''}
                        />
                        }/>
                        
                        <Controller
                            name="todoDescription"
                            control={control}
                            defaultValue={""}
                            render={({field}) => <TextField
                                {...field}
                                className="textArea"
                                label="Todo Description"
                                variant="outlined"
                                placeholder={"Todo Description"}
                                error={!!errors.todoDescription}
                                type="text"
                                multiline
                                helperText={errors?.todoDescription ? errors?.todoDescription?.message : ''}
                            />
                        }/>

                        <RHFSelect
                        id="todo-type-selection"
                        name="todoType"
                        className={"todo-type-selection"}
                        label="Todo Type"
                        control={control}
                        defaultValue={0}
                        variant="outlined"
                        margin="normal">
                            {TodoTypes.map(type=><MenuItem key={type.value} value={type.value}>{type.text}</MenuItem>)}
                        </RHFSelect>

                        <Stack
                        spacing={1}
                        direction="row"
                        justifyContent="flex-end">
                            <Button type="submit" variant="outlined">Save</Button>
                            <Button onClick={()=>setEditTodoOpen(false)} type="button" variant="outlined">Cancel</Button>
                        </Stack>

                    </Stack>
                </form>
            </Box>
        </Modal>
    </div>
}

export default EditTodo;