//import NotesAside from '.components/NotesAside/NotesAside';//cut it and paste to the component
import React from 'react';
import NotesList from './NotesList';
import Stack from '@mui/material/Stack';
import CloseIcon from '@mui/icons-material/Close';

const NotesAside = ({
    showNotes,
    setShowNotes,
    fixed=false,
    enableCopyText,
    enableAddTodo,
}) => {
    return <div className={`NotesAsideContainer ${fixed ? 'fixed' : ""} ${showNotes ? 'open' : ""}`}>
        <Stack
        direction="row"
        justifyContent="space-between"
        alignItems="flex-start"
        spacing={2}
        style={{marginBottom:25}}
        className="aside-data">
            <h2 className="section-title-01">Notes</h2>
            <CloseIcon 
            className="close-form-trigger"
            onClick={()=>setShowNotes(false)}/>
        </Stack>
        {showNotes ?    <NotesList
                        enableCopyText={enableCopyText}
                        enableAddTodo={enableAddTodo}
                        /> : ""}
    </div>
}

export default NotesAside;