//import MomentForm from '.components/MomentForm/MomentForm';//cut it and paste to the component
import React, {useEffect} from 'react';
import './MomentForm.scss';
import * as yup from "yup";
import { useForm, Controller } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import CancelIcon from '@mui/icons-material/Cancel';
// import IconButton from '@mui/material/IconButton';
import Axios from "axios";
import { MomentType, getMomentType } from "../Hooks/Constants"
import { useSelector,useDispatch } from 'react-redux';
import {
    actionAddMoment,
    actionEditMoment
} from '../../app/Reducers/todo/todoSlice';
import { ReactComponent as OvertimeIcon } from '../../Assets/ic_overtime.svg';
import { ReactComponent as SubmitIcon } from '../../Assets/ic_submit.svg';
import { ReactComponent as IdeaIcon } from '../../Assets/ic_idea.svg';
import { ReactComponent as ChatIcon } from '../../Assets/ic_chat.svg';

const schema = yup.object().shape({
    moment: yup.string().required()
});


const MomentForm = ({
    todoId,
    momentToEdit,
    setMomentToEdit,
    // setMomentList
}) => {

    /* Just a test code... remove it afterwards */ 
    const URI = "https://reflect-backend-2.azurewebsites.net";//process.env.DEV_MONGO_URL;
    /* Just a test code... remove it afterwards */ 

    const [momentType, setMomentType] = React.useState(MomentType.comment);
    const dispatch = useDispatch()

    // form setup
    const { control, handleSubmit, reset, formState: {errors,isSubmitSuccessful} } = useForm({ 
        defaultValues: {
            moment: ""
        },
        resolver: yupResolver(schema)
    });

    // reset the form after successfull submit of form data
    useEffect(() => {
        if (isSubmitSuccessful) {
            reset({ moment: "" });
        };
    }, [isSubmitSuccessful, reset]);

    useEffect(()=>{
        if(momentToEdit){
            
            reset({ moment: momentToEdit ? momentToEdit.moment : "" });
            setMomentType(momentToEdit.momentType)
            // console.log("testing the moment to edit")
            // console.log(momentToEdit.momentType)
        }
    },[momentToEdit])

    // on form submit
    const onSubmit = async ({moment,description}) => {
        console.log(moment)
        // console.log(description)
        // submit the form here using api
        if(momentToEdit){
            console.log("toedit")
            console.log(momentToEdit)
            await dispatch(actionEditMoment({
                momentId: momentToEdit._id,
                todoId,
                updatedMomentText: moment,
                momentType
            }))
            setMomentToEdit(null)
        }
        else{
            console.log("toadd")
            dispatch(actionAddMoment({
                todoId,
                moment,
                momentType
            }))

            // Axios.put(`${URI}/todos/add-moment`,{
            //     todoId,
            //     moment,
            //     momentType
            // }).then(res => {
            //     setMomentList(res.data ? res.data.moments : [])
            // })
        }
    }


    return <div className='MomentFormContainer'>
        <form className="moment-create-form" onSubmit={handleSubmit(onSubmit)}>
            <Stack spacing={2}>
                <Stack direction="row" alignItems="center" justifyContent="space-between" spacing={2}>
                    <p className="moment-form-title">Moment</p>
                    <ul className="moment-choices">
                        <li 
                        className={`${momentType === MomentType.comment ? "active" : ""}`}
                        onClick={()=>setMomentType(MomentType.comment)}>
                            <ChatIcon/>
                        </li>
                        <li 
                        className={`${momentType === MomentType.insight ? "active" : ""}`}
                        onClick={()=>setMomentType(MomentType.insight)}>
                            <IdeaIcon/>
                        </li>
                        <li 
                        className={`${momentType === MomentType.challenge ? "active" : ""}`}
                        onClick={()=>setMomentType(MomentType.challenge)}>
                            <OvertimeIcon/>
                        </li>
                    </ul>
                </Stack>
                <Controller
                    name="moment"
                    control={control}
                    defaultValue={""}
                    render={({field}) => <TextField
                        {...field}
                        className="textArea"
                        label="Moment description"
                        variant="outlined"
                        placeholder={"Moment description"}
                        error={!!errors.moment}
                        type="text"
                        multiline
                        helperText={errors?.moment ? errors?.moment?.message : ''}
                        sx = {{marginTop: "12px"}}
                    />
                }/>
                <Stack
                direction="row"
                alignItems="center"
                justifyContent="space-between"
                spacing={2}>
                    <p className="moment-type-label">{getMomentType(momentType)}</p>
                    {momentToEdit ? <>
                        <Button 
                            component={
                                React.forwardRef((props, ref) => <button type="button" {...props} ref={ref} />)
                            }
                            onClick={()=>setMomentToEdit(null)} 
                            variant="contained" 
                            endIcon={<CancelIcon />}
                            sx={{height:"30px"}}>Cancel</Button>
                        <Button
                            type="submit"
                            variant="contained"
                            endIcon={<SubmitIcon/>}
                            sx={{height:"30px"}}>Update</Button>
                    </>:<>
                        <Button
                            type="submit"
                            variant="contained"
                            endIcon={<SubmitIcon/>}
                            sx={{height:"30px"}}>Submit</Button>
                    </>}
                    
                </Stack>
            </Stack>
        </form>
    </div>
}

export default MomentForm;