export const getTodoStatus = statusCode => {
    switch(statusCode){
        case 0 : return {
            color: "#637381",
            background: "rgba(rgba(99, 115, 129, 0.16))",
            text: "Not Started"
        }
        case 1 : return {
            color: "#00ab55",
            background: "rgba(0, 171, 85, 0.08)",
            text: "On Going"
        }
        case 2 : return {
            color: "#b78103",
            background: "rgba(255, 193, 7, 0.16)",
            text: "Paused"
        }
        case 3 : return {
            color: "#1890FF",
            background: "rgba(24, 144, 255, 0.08)",
            text: "Done"
        }
    }
    return {
        color: "#637381",
        background: "rgba(rgba(99, 115, 129, 0.16))",
        text: "Not Started"
    }
}



export const MomentType = {
    comment: 1,
    insight: 2,
    challenge: 3,
    reflection: 4
}

export const getMomentType = statusCode => {
    switch(statusCode){
        case 1 : return "Comment"
        case 2 : return "Insight"
        case 3 : return "Challenge"
        case 4 : return "Reflection"
    }
}

export const TodoTypes = [
    {value: 0, text: "Working"},
    {value: 1, text: "Learning"},
    {value: 2, text: "Learning and working"},
    {value: 3, text: "Administration"} 
]

export const getTodoType = todoType => {
    switch(todoType){
        case 0 : return "Working"
        case 1 : return "Learning"
        case 2 : return "Learning and working"
        case 3 : return "Administration"
    }
}
