//import Login from '.components/Login/Login';//cut it and paste to the component
import React from 'react';
import './LoginForm.scss';
import * as yup from "yup";
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import {
    actionAuthenticateToken,
    actionLogin
} from '../../app/Reducers/user/userSlice';
import { useForm, Controller } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { useSelector, useDispatch } from 'react-redux';
import Stack from '@mui/material/Stack';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';

const schema = yup.object().shape({
    email: yup.string().required("Provide valid email"),
    password: yup.string()
                .required('No password provided.') 
                .min(8, 'Password is too short - should be 8 chars minimum.')
});

const Login = ({
    setView
}) => {

    const dispatch = useDispatch()

    // form setup
    const { setError, control, handleSubmit, reset, formState: {errors,isSubmitSuccessful} } = useForm({ 
        defaultValues: {
            // email: thirdPartyUserData ? thirdPartyUserData.loginHint : "",
            email: "",
            password: ""
        },
        resolver: yupResolver(schema)
    });

    const onSubmit = async registrationThirdPartyUserData => {
        const {email, password} = registrationThirdPartyUserData;
        const loggedIn = await dispatch(actionLogin({email, password}))
        if(!loggedIn){
            [
                {
                  type: "manual",
                  name: "email",
                  message: "Wrong email and password"
                },
                {
                  type: "manual",
                  name: "password",
                  message: "Wrong email and password"
                }
            ].forEach(({ name, type, message }) =>
                setError(name, { type, message })
            );
        }
    }

    return <div className='LoginContainer form-wrapper'>
        <form className="login-form form-container" onSubmit={handleSubmit(onSubmit)}>
            <h1 className="title">Login</h1>
            <Stack
            direction="column"
            alignItems="center"
            spacing={2}>
                <Controller
                    name="email"
                    control={control}
                    defaultValue={""}
                    render={({field}) => <TextField
                        {...field}
                        className="textArea"
                        label="Email"
                        variant="outlined"
                        placeholder={"email"}
                        error={!!errors.email}
                        type="email"
                        sx={{ width: '100%' }}
                        helperText={errors?.email ? errors?.email?.message : ''}
                    />
                }/>
                <Controller
                    name="password"
                    control={control}
                    defaultValue={""}
                    render={({field}) => <TextField
                        {...field}
                        className="textArea"
                        label="Password"
                        variant="outlined"
                        placeholder={"password"}
                        error={!!errors.password}
                        type="password"
                        sx={{ width: '100%' }}
                        helperText={errors?.password ? errors?.password?.message : ''}
                    />
                }/>
                <div className="forgot-password-and-login-options">
                    <FormGroup>
                        <FormControlLabel control={<Checkbox defaultChecked />} label="Remember Me" />
                    </FormGroup>
                    <p><span className="forgot-password-link" onClick={()=>{alert("Under construction")}}>Forgot Password?</span></p>
                </div>
                <Button
                type="submit"
                variant="contained"
                color="primary"
                style={{
                    width: "100%",
                    borderRadius: "40px",
                    height: "48px",
                    fontSize: "16px",
                }}>Login</Button>
                <p>Not registered yet? <span className="registration-link" onClick={()=>{setView("registration")}}>Create an Account</span></p>
            </Stack>

        </form>

        <Button
        onClick={async () => {
            dispatch(actionAuthenticateToken())
        }}
        type="button"
        variant="outlined">
            Test query
        </Button>
    </div>
}

export default Login;