export const actionRecordPlan = (user, todoList, calendarPlanData, selectedDateRange = []) => async (dispatch,getState) => {
    
    const headers = getHeaders();
    if(headers == null) return
    
    Axios.post(
        `${URI}/planning/create`,
        {
            user: user,
            todoList: todoList,
            calendarPlanData: calendarPlanData,
            selectedDateRange: selectedDateRange
        },
        headers
    ).then(res => {
        console.log(res)
    })
    
}

