import React, { useEffect } from 'react';
import Slider from "react-slick";
import './PostSlider.scss';
import "slick-carousel/slick/slick.css";
// import "slick-carousel/slick/slick-theme.css";


const PostSlider = ({
    children,
    numberOfSlides,
    autoplay
}) => {
    var settings = {
        dots: true,
        arrows: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: autoplay,
        autoplaySpeed: 5000,
        pauseOnHover: true,
        adaptiveHeight: true
    };
    useEffect(() => { }, [numberOfSlides, autoplay])

    return <div className='PostSliderContainer'>
        <Slider {...settings}>
            {children}
            {/* {[1,2,3,4].map(e=><Post key={e}/>)} */}
        </Slider>
    </div>
}

export default PostSlider;