//import TaskView from '.components/TaskView/TaskView';//cut it and paste to the component
import React, { useState } from 'react';
import './TaskView.scss';
import WingenioCalendar from "../../components/WingenioCalendar/WingenioCalendar";
import "../../components/GlobalStyles/GlobalStyles.scss";
import TodoList from "../../components/Todo/TodoList";
import NotesAside from '../../components/Notes/NotesAside';
import TodoSideNav from '../../components/Todo/TodoSideNav';
import { useSelector, useDispatch } from 'react-redux';
import {
  actionAddBreaktime
} from '../../app/Reducers/todo/todoSlice';

import { ReactComponent as BreakIcon } from '../../Assets/ic_break.svg';
import { ReactComponent as NotesIcon } from '../../Assets/ic_notes.svg';

const TaskView = ({

}) => {

    const dispatch = useDispatch()

    const [showNotes, setShowNotes] = useState(false)
    const [selectedSchedule, setSelectedSchedule] = useState(null)
    const [asideTodoOpen, setAsideTodoOpen] = useState(true)
    const [schedules,setSchedules] = useState([])
    const {
        todoList
    } = useSelector(state => state.todo);
    
    const updateSchedule = scheduleUpdate => {
        setSchedules(schedules.map(schedule => {
            if(scheduleUpdate.id === schedule.id){
                return scheduleUpdate;
            }
            return schedule
        }));
    }

    return <div className='TaskViewContainer'>
        
        <TodoList todoList={todoList} defaultShow={true}/>

        <div className="calendar-wrapper">
            <div className="todo-header-container">
                <h2 className="section-title-01">Day tasks Record</h2>
                <ul className="actions-container">
                    <li onClick={()=>setShowNotes(show=>!show)}>
                        <span className="icon"><NotesIcon/></span>
                        <span className="text">Task Notes</span>
                    </li>
                    <li onClick={()=>dispatch(actionAddBreaktime())} >
                        <span className="icon"><BreakIcon/></span>
                        <span className="text">Lunch Break</span>
                    </li>
                </ul>
            </div>
            <WingenioCalendar />
        </div>

        <NotesAside
            showNotes={showNotes}
            setShowNotes={setShowNotes}
            fixed={true}
            enableAddTodo={true}
        />

        <TodoSideNav
            schedule={selectedSchedule}
            open={asideTodoOpen} 
            setAsideTodoOpen={setAsideTodoOpen}
            updateSchedule={updateSchedule}
            setSelectedSchedule={setSelectedSchedule}
        />
    </div>
}

export default TaskView;