import React, {useState,useEffect,useMemo} from 'react';
import './Dashboard.scss';
import BurnoutIndex from './BurnoutIndex/BurnoutIndex';
import WeeklyAverageData from './WeeklyAverageData/WeeklyAverageData';
import PostSlider from '../../components/PostSlider/PostSlider';
import Paper from '@mui/material/Paper';
import DoughnutChartWithTableSection from './DoughnutChartWithTableSection';
import WeeklyBurnoutIndex from './WeeklyBurnoutIndex';
import ProductivityAndEventContent from './ProductivityAndEventContent';
import Stack from '@mui/material/Stack';
import { useSelector, useDispatch } from 'react-redux';
import moment from 'moment';
import {
    actionGetAverageCountData
} from '../../app/Reducers/dashboard/dashboardSlice';
import {
    actionGetBreaktimeOnDateRange
} from '../../app/Reducers/todo/todoSlice';
import TodoList from "../../components/Todo/TodoList";

const Dashboard = ({
    // dashboardOpen,
    // setDashboardOpen
}) => {
    const doughnutColorPallete = [
        "#4472c4",
        "#ed7d31",
        "#a5a5a5",
        "#ffc001",
        "#5b9bd5",
        "#70ad47",
        "#264478",
        "#e9452a",
        "#33acaf",
        "#7b6862",
        "#06c",
        "#4cb140",
        "#009596",
        "#5752d1",
        "#f4c145",
        "#ec7a07",
        "#8bc1f7",
        "#23511e",
        "#a2d9d9",
        "#2a265f",
        "#bde2b9",
        "#003737",
        "#b2b0ea",
        "#c58c01",
        "#f4b678",
        "#519de9",
        "#38812f",
        "#73c5c5",
        "#3c3d99",
        "#f6d173",
        "#c46101",
        "#004b95",
        "#7cc674",
        "#005f60",
        "#8481dd",
        "#f0ab00",
        "#ef9234",
    ];

    const {
        dailyProductivityData,
        todoTypes,
        todoStatus,
        allTaskWithinDateRange
    } = useSelector(state => state.dashboard);

    const {
        userData
    } = useSelector(state => state.user);

    const dispatch = useDispatch();

    useEffect(()=>{
        const endDate = new Date();
        const formattedEndDate = `endDate=${moment(endDate).format('YYYY-MM-DD')}`;
        dispatch(actionGetAverageCountData(userData._id,`${formattedEndDate}`))
    },[])

    const [statusFilter, setStatusFilter] = useState(-1)
    const [typesFilter, setTypesFilter] = useState(-1)
    const [parentControlShow, setParentControlShow] = useState(false)

    const statusGraphLabels = useMemo(() => {
            
        const colorPallete = ["#637381", "#00ab55", "#fec02f", "#1890FF"]
        const unit = "todos"

        return <ul className="custom-data-legends">
            {
                todoStatus?.map(
                (x, i) =>   <li 
                            key={i}
                            className={`legend-data ${statusFilter == x.statusCode ? "active" : ""}`}
                            onClick={() => {
                                setStatusFilter(statusFilter == x.statusCode ? -1 : x.statusCode)
                                setParentControlShow(true)
                            }}>
                                <span className="labelColor" style={{ backgroundColor: `${colorPallete[i % colorPallete.length]}` }}>.</span>
                                <span className="labelText">{x.label} ({x.count}{unit && " " + unit})</span>
                            </li>
                )
            }
        </ul>

    }, [todoStatus,statusFilter,typesFilter]);

    const typesGraphLabels = useMemo(() => {
            
        const colorPallete = ["#00ab55", "#fec02f", "#1890FF", "#637381"]
        const unit = "todos"

        return <ul className="custom-data-legends">
            {
                todoTypes?.map(
                (x, i) =>   <li 
                            key={i}
                            className={`legend-data ${typesFilter == x.typeCode ? "active" : ""}`}
                            onClick={() => {
                                setTypesFilter(typesFilter == x.typeCode ? -1 : x.typeCode)
                                setParentControlShow(true)
                            }}>
                                <span className="labelColor" style={{ backgroundColor: `${colorPallete[i % colorPallete.length]}` }}>.</span>
                                <span className="labelText">{x.label} ({x.count}{unit && " " + unit})</span>
                            </li>
                )
            }
        </ul>

    }, [todoStatus,statusFilter,typesFilter]);

    const closeTodoList = () => {
        setStatusFilter(-1)
        setTypesFilter(-1)
        setParentControlShow(false)
    }

    if(!userData) return ""

    return <div className={`DashboardContainer ${parentControlShow ? "show-task-list" : ""}`}>
        <div className="todo-list-wrapper">
            <TodoList
                todoList={
                    allTaskWithinDateRange.filter(task => {
                        if( statusFilter == -1 && typesFilter == -1 ) return true
                        if( statusFilter > -1 && typesFilter > -1 ) return task.status == statusFilter && task.type == typesFilter
                        if( statusFilter == -1 &&  typesFilter > -1 ) return task.type == typesFilter
                        if( statusFilter > -1 && typesFilter == -1 ) return task.status == statusFilter
                    })
                }
                defaultShow={false}
                parentControlShow={parentControlShow}
                callBack={closeTodoList}
            />
        </div>

        <Stack 
        direction="row"
        justifyContent="space-between"
        alignItems="center"
        spacing={2}
        mb={2}>
            <h2 onClick={()=>{
                const endDate = new Date();
                const formattedEndDate = `endDate=${moment(endDate).format('YYYY-MM-DD')}`;
                dispatch(actionGetAverageCountData(userData._id,`${formattedEndDate}`))
            }}>Dashboard</h2>

            <p onClick={()=>{
                const endDate = new Date();
                const formattedEndDate = `endDate=${moment(endDate).format('YYYY-MM-DD')}`;
                dispatch(actionGetBreaktimeOnDateRange(formattedEndDate))
            }}>Testing the calendar</p>

        </Stack>
        <div className="top-panel">
            <div className='WeeklyGraphSliderContainer'>
                <PostSlider>
                    <ProductivityAndEventContent/>
                    <WeeklyBurnoutIndex/>
                </PostSlider>
            </div>
            <WeeklyAverageData/>
        </div>
        
        <div className="todo-types-and-status-info-wrapper">
            
            <div className="todo-types-chart">
                <Paper className="doughnut-chart-wrapper paperStyles">
                    <DoughnutChartWithTableSection
                        graphData={[...todoTypes]}
                        colorPallete={["#00ab55", "#fec02f", "#1890FF", "#637381"]}
                        totalGraphData={24}
                        isPercentageData={false}
                        unit="todos"
                        title="Breakdown of Todo Types"
                        chartWidth={230}
                        chartHeight={230}
                        filterZeroValues={false}
                        legendsList={typesGraphLabels}
                    />
                </Paper>
            </div>

            <div className="todo-status-chart">
                <Paper className="doughnut-chart-wrapper paperStyles">
                    <DoughnutChartWithTableSection
                        graphData={[...todoStatus]}
                        colorPallete={["#637381", "#00ab55", "#fec02f", "#1890FF"]}
                        totalGraphData={24}
                        isPercentageData={false}
                        unit="todos"
                        title="Breakdown of Todo Status"
                        chartWidth={230}
                        chartHeight={230}
                        filterZeroValues={false}
                        legendsList={statusGraphLabels}
                    />
                </Paper>
            </div>
    
        </div>

        <div className="bottom-panel">
            <BurnoutIndex/>
            <Paper className="productivity-data-container member-data-container paperStyles">
                <DoughnutChartWithTableSection
                    graphData={[...dailyProductivityData]}
                    colorPallete={doughnutColorPallete}
                    totalGraphData={24}
                    isPercentageData={false}
                    unit="Hrs"
                    title="Breakdown Of Productivity"
                    chartWidth={230}
                    chartHeight={230}
                />
            </Paper>
        </div>

        </div>
}

export default Dashboard;