//import LineGraph from '.components/LineGraph/LineGraph';//cut it and paste to the component
import React from 'react';
import {
    XAxis,
    FlexibleWidthXYPlot,
    YAxis,
    HorizontalGridLines,
    LineSeries
} from 'react-vis';
import './LineGraph.scss';

const LineGraph = ({
    graphDataArray,
    colorPallete
}) => {
    return graphDataArray ? <div className="line-and-bar-graph">
        <div className="graphContainer">
            <FlexibleWidthXYPlot
                // width={width}
                animation
                height={300}
                xType="ordinal">
                {/* <VerticalGridLines style={{ stroke: '#B7E9ED' }} /> */}
                <HorizontalGridLines style={{ stroke: '#dedede' }} />
                <XAxis
                    title="Date"
                    style={{
                        line: { stroke: '#ADDDE1' },
                        ticks: { stroke: '#ADDDE1' },
                        text: { stroke: 'none', fill: '#6b6b76', fontWeight: 600 }
                    }}
                    tickLabelAngle={-35}
                />
                <YAxis />

                {graphDataArray.map((graphData,i) => <LineSeries key={graphData.id}
                    className=""
                    style={{ strokeWidth: '2px', stroke: colorPallete[i] }}
                    data={graphData.data}
                />)}
            </FlexibleWidthXYPlot>
        </div>
        <ul className="dataLegends" style={{ width: "300px" }}>
            {graphDataArray.map((e, i) => <li key={i} className="legend-container">
                <span
                    className="legend"
                    style={{
                        backgroundColor: colorPallete[i],
                        height: "2px"
                    }}
                >.</span>
                <span className="label">{e.name}</span>
            </li>)}
        </ul>
    </div> : ""
}

export default LineGraph;