//import Notes from '.components/Notes/Notes';//cut it and paste to the component
import React, {useState} from 'react';
import NoteEditor from './NoteEditor';
import IconButton from '@mui/material/IconButton';
import EditIcon from '@mui/icons-material/Edit';
import CustomHTMLParser from "../Hooks/CustomHTMLParser";
import JSONToReact from "../Hooks/JSONToReact";
import TitleIcon from '@mui/icons-material/Title';
import DeleteIcon from '@mui/icons-material/Delete';
import Stack from '@mui/material/Stack';
import DeleteNote from './DeleteNote';
import EditNote from './EditNote';
import { useSelector,useDispatch } from 'react-redux';
import {
    actionAddTodoInList
} from '../../app/Reducers/todo/todoSlice';
import {
    actionUpdateNotes
} from '../../app/Reducers/note/noteSlice';

const Notes = ({
    note,
    updateNote,
    deleteNote,
    enableCopyText,
    enableAddTodo,
}) => {
   
    const [noteContent, setNoteContent] = useState(note.content);
    const [showNoteEditor, setShowNoteEditor] = useState(false);
    const [jsonNote, setJsonNote] = useState(JSON.parse(CustomHTMLParser(note.content)));
    const [deleteConfirmationOpen, setDeleteConfirmationOpen] = useState(false)
    const [editNoteConfirmationOpen, setEditNoteConfirmationOpen] = useState(false)
    const dispatch = useDispatch()

    const createTodo = todoName => {
        dispatch(actionAddTodoInList({
            name: todoName,
            description: todoName
        }))
    };

    const onSaveNote = updatedNote => {
        console.log(updatedNote)
        // convert html string generated by the ck editor to json data
        const rawJsonData = CustomHTMLParser(updatedNote);
        console.log(rawJsonData)
        setNoteContent(updatedNote); // set the note data
        setShowNoteEditor(false); // close the editor
        setJsonNote(JSON.parse(rawJsonData));
        dispatch(actionUpdateNotes({
            ...note,
            content: updatedNote
        }));
    }

    return (
    <div className='NotesContainer'>
        {/* {notes} */}
        <div className="note-wrapper">

            <div className="note-header">
                <h2>{note.title} Notes</h2>
                <Stack
                direction="row"
                justifyContent="flex-end"
                alignItems="center"
                spacing={1}>
                    <IconButton
                    className={"edit-note-trigger"}
                    onClick={()=>setShowNoteEditor(show=>!show)}
                    aria-label="edit"
                    size="small">
                        <EditIcon fontSize="inherit" />
                    </IconButton>

                    <IconButton
                    className={"edit-title-note-trigger"}
                    onClick={() => setEditNoteConfirmationOpen(true) }
                    aria-label="Update title"
                    size="small">
                        <TitleIcon fontSize="inherit" />
                    </IconButton>

                    <IconButton
                    className={"delete-note-trigger"}
                    onClick={() => setDeleteConfirmationOpen(true) }
                    aria-label="Delete title"
                    size="small">
                        <DeleteIcon fontSize="inherit" />
                    </IconButton>
                </Stack>
                
            </div>

            <div className="note-content">
                {noteContent.length ?   <JSONToReact 
                                        htmlJson={jsonNote}
                                        onTextElementClick={createTodo}
                                        enableCopyText={enableCopyText}
                                        enableAddTodo={enableAddTodo}
                                        /> : ""}
            </div>

        </div>
        
        {showNoteEditor ? (
            <NoteEditor
            notes={noteContent}
            onSaveNote={onSaveNote}
            setShowNoteEditor={setShowNoteEditor}/>
        ) : ""}

        <EditNote
            note={note}
            editNoteConfirmationOpen={editNoteConfirmationOpen}
            setEditNoteConfirmationOpen={setEditNoteConfirmationOpen}
            updateNote={updateNote}
        />

        <DeleteNote
            note={note}
            deleteConfirmationOpen={deleteConfirmationOpen}
            setDeleteConfirmationOpen={setDeleteConfirmationOpen}
            deleteNote={deleteNote}
        />
    </div>
    )
}

export default Notes;