import React, {useState} from 'react';
import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineDot from '@mui/lab/TimelineDot';
import TimelineOppositeContent from '@mui/lab/TimelineOppositeContent';
import moment from 'moment';
import Paper from '@mui/material/Paper';
import { getMomentType } from "../Hooks/Constants"
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemIcon from '@mui/material/ListItemIcon';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import IconButton from '@mui/material/IconButton';

import './MomentList.scss';

const MomentList = ({
    momentList,
    setMomentToEdit
}) => {
    const [anchorEl, setAnchorEl] = useState(null);
    const [selectedMoment,setSelectedMoment] = useState(null);

    const open = Boolean(anchorEl);

    const momentMoreOption = (event,moment) => {
        setAnchorEl(event.currentTarget)
        setSelectedMoment(moment)
    };

    const deleteMoment = () => {
        console.log(`Delete this moment ${selectedMoment}`)
    }

    const handleClose = () => {
        setAnchorEl(null)
        setSelectedMoment(null)
        setMomentToEdit(null)
    };

    const menuOptions = [
        {
            id: 1,
            name: "Edit",
            icon: <EditIcon fontSize="small"/>,
            callback: () => setMomentToEdit(selectedMoment) // parent component function
        },
        {
            id: 2,
            name: "Delete",
            icon: <DeleteIcon fontSize="small"/>,
            callback: () => deleteMoment() // component function
        }
    ];

    const getMenuItems = () => {
        return menuOptions.map(option=>(
            <MenuItem
            key={option.id}
            onClick={()=>{
                option.callback()
                setAnchorEl(null)
            }}>
                <ListItemIcon>
                    {option.icon}
                </ListItemIcon>
                <ListItemText>{option.name}</ListItemText>
            </MenuItem>
        ))
    }

    return <div className='MomentListContainer'>
        <h3 className="my-moments">MOMENTS</h3>
        <div className="moment-list-wrapper">
            <Timeline>
                {momentList?.map(momentItem=>{
                    return <TimelineItem key={momentItem._id}>
                        <TimelineOppositeContent
                        sx={{ m: '8px 0', p: `0 18px 0 0`, flex: `0 0 100px` }}
                        variant="body2"
                        color="text.secondary"
                        >
                            <div className="date-wrapper">
                                <p className="date">{moment(momentItem.createdAt).format('L')}</p>
                                <p className="time">{moment(momentItem.createdAt).format('LT')}</p>
                            </div>
                        </TimelineOppositeContent>
                        <TimelineSeparator>
                        <TimelineDot 
                        sx={{
                            backgroundColor: "#00AB55"
                        }}/>
                        <TimelineConnector />
                        </TimelineSeparator>
                        <TimelineContent>
                            <Paper elevation={3}>
                                <div className="momentItem">
                                    <span className="moment-type">{getMomentType(momentItem.momentType)}</span>
                                    <p className="momentItem-desc">{momentItem.moment}</p>
                                    <IconButton
                                    className="moment-more-option-trigger"
                                    aria-controls={open ? 'basic-menu' : undefined}
                                    aria-haspopup="true"
                                    aria-expanded={open ? 'true' : undefined}
                                    onClick={event => momentMoreOption(event,momentItem)}>
                                        <MoreHorizIcon/>
                                    </IconButton>
                                </div>
                            </Paper>
                        </TimelineContent>
                    </TimelineItem>
                })}
            </Timeline>

            <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                MenuListProps={{
                'aria-labelledby': 'basic-button',
                }}
            >
                {getMenuItems()}
            </Menu>
        </div>
    </div>
}

export default MomentList;